(function () {
  'use strict';
  angular.module('photogram').directive('actualSrc', function () {
    return {
      link: function postLink(scope, element, attrs) {
        attrs.$observe('actualSrc', function (newVal) {
          if (newVal !== undefined) {
            var img = new Image();
            img.src = attrs.actualSrc;
            angular.element(img).bind('load', function () {
              element.attr('src', attrs.actualSrc);
            });
          }
        });
      }
    }
  });

})();

(function () {
    'use strict';

    angular.module('photogram').directive('compareTo', compareToDirective);

    function compareToDirective() {
        return {
            restrict: 'A',
            scope   : {
                targetModel: '=compareTo'
            },
            require : 'ngModel',
            link    : function postLink(scope, element, attrs, ctrl) {

                var compare = function () {

                    var e1 = element.val();
                    var e2 = scope.targetModel;

                    if (e2 !== null) {
                        return e1 === e2;
                    }

                    return false;
                };

                scope.$watch(compare, function (newValue) {
                    ctrl.$setValidity('errorCompareTo', newValue);
                });

            }
        };
    }

})();
(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name deleteItem
   *
   * @description
   * _Please update the description and restriction._
   *
   * @restrict A
   * */

  angular
    .module('photogram')
    .directive('deleteItem', deleteItemDirective);

  function deleteItemDirective($mdDialog, Toast) {
    return {
      restrict: 'A',
      link    : deleteItemLink,
      scope   : {
        model   : '=',
        item    : '=',
        onDelete: '='
      }
    };

    function deleteItemLink(scope, elem, attr) {

      elem.bind('click', function (event) {

        var confirm = $mdDialog.confirm()
                               .title('Confirm action')
                               .content('Are you sure you want to delete this item?')
                               .ok('Delete')
                               .cancel('Cancel')
                               .targetEvent(event)
          ;

        $mdDialog.show(confirm).then(function () {

          scope.Model.destroy(item).then(function (success) {
              Toast.simple('Item deleted.');
              scope.onDelete();
            },
            function (error) {
              Toast.simple(error.message);
            });

        });
      });


    }
  }

})();

