(function () {
    'use strict';

    angular.module('photogram').directive('facebookLogin', facebookLoginDirective);

    function facebookLoginDirective($state, $q, $translate, Auth, Facebook, User, $rootScope, Toast) {
        return {
            restrict: 'E',
            scope: {
                loading: '='
            },
            link    : facebookLoginLink,
            template: '<md-button class="md-raised button-facebook" aria-label="{ name || \'loginWithFacebook\'| translate}"> <md-icon md-font-icon="icon-facebook"></md-icon> <span>{{ name || \'loginWithFacebook\'| translate }}</span> </md-button>'
        };

        function facebookLoginLink(scope, elem, attr) {

            scope.facebookStatus = null;
            Facebook.getCurrentUser().then(function (resp) {
                console.log(resp);
                scope.facebookStatus = resp;
                return resp;
            }).then(function (facebookStatus) {
                if (facebookStatus.status === 'connected') {
                    Facebook.me().then(function (userFacebook) {
                        scope.name = userFacebook.name;
                    });
                }
            }).catch(function () {
                console.log('Not logged');
            });

            elem.bind('click', facebookLogin);

            function facebookLogin() {

                scope.loading = true;
                console.log('Login Facebook');
                Facebook.getCurrentUser().then(function (fbAuthData) {
                    console.log(fbAuthData);

                    if (fbAuthData.status === 'connected') {
                        var fbData = null;
                        return Facebook.me().then(function (data) {
                            fbData = data;
                            return User.findByEmail(data.email);
                        }).then(function (user) {
                            if (!user.id) {
                                return User.signInViaFacebook(fbAuthData);
                            } else if (user.get('authData')) {
                                if (user.get('authData').facebook.id === fbData.id) {
                                    return User.signInViaFacebook(fbAuthData);
                                }
                            } else {
                                var deferred = $q.defer();
                                deferred.reject($translate.instant('emailFacebookTakenText'));
                                return deferred.promise;
                            }
                        }).then(function () {
                            return User.updateWithFacebookData(fbData);
                        }).then(function (user) {
                            console.log(user);
                            Toast.simple($translate.instant('loggedInAsText') + ' ' + user.get('email'));
                            $rootScope.currentUser = Auth.getLoggedUser();
                            $state.go('app.feed');
                            scope.loading = false;
                        }, function (error) {
                            Toast.simple(error);
                            scope.loading = false;
                        });
                    }
                });


                function processFacebookLogin(fbAuthData) {


                    var fbData = null;

                    return Facebook.me().then(function (data) {
                        fbData = data;
                        return User.findByEmail(data.email);
                    }).then(function (user) {
                        if (!user.id) {
                            return User.signInViaFacebook(fbAuthData);
                        } else if (user.get('authData')) {
                            if (user.get('authData').facebook.id === fbData.id) {
                                return User.signInViaFacebook(fbAuthData);
                            }
                        } else {
                            var deferred = $q.defer();
                            deferred.reject($translate.instant('emailFacebookTakenText'));
                            return deferred.promise;
                        }
                    }).then(function () {
                        return User.updateWithFacebookData(fbData);
                    }).then(function (user) {
                        Toast.show($translate.instant('loggedInAsText') + ' ' + user.get('email'));
                    }, function (error) {
                        Toast.alert(error);
                    });
                }

            }

        }
    }

})();

