(function () {
  'use strict';
  
  angular.module('photogram').directive('numbersOnly', function () {
    return {
      require: 'ngModel',
      link   : function (scope, element, attr, ngModelCtrl) {
        function fromUser(text) {
          if (text) {
            var transformedInput = text.replace(/[^0-9]/g, '');

            if (transformedInput !== text) {
              ngModelCtrl.$setViewValue(transformedInput);
              ngModelCtrl.$render();
            }
            return transformedInput;
          }
          return undefined;
        }

        ngModelCtrl.$parsers.push(fromUser);
      }
    };
  });

})();

(function () {
    'use strict';

    angular.module('photogram').directive('uploadImage', uploadImageDirective);

    function uploadImageDirective($q) {
        return {
            restrict: 'A',
            link    : uploadImageLink
        };

        function uploadImageLink(scope, elem, attr) {
            elem.bind('click', function () {
                var defer     = $q.defer();
                var fileInput = angular.element('<input type="file" accept="image/x-png, image/gif, image/jpeg" max-size="2048" />');
                fileInput[0].click();
                fileInput.on('change', function (evt) {
                    var tempImage = evt.currentTarget.files[0];
                    var reader    = new FileReader();
                    reader.onload = function (evt) {
                        defer.resolve(evt.target.result);
                    };
                    reader.readAsDataURL(tempImage);
                });
                return defer.promise;
            })
        }
    }

})();
