(function () {
    'use strict';
    angular.module('admin')
           .controller('AdminUserCtrl', AdminUserController)
           .controller('DialogUserController', DialogUserController);

    function AdminUserController(User, $scope, $mdDialog, $mdToast, Auth) {

        // Pagination options.
        $scope.rowOptions = [10, 20, 40];

        $scope.query = {
            filter: '',
            order : '-createdAt',
            limit : 40,
            page  : 1,
            total : 0
        }

        $scope.users = [];

        var showToast = function (message) {
            $mdToast.show(
                $mdToast.simple()
                        .content(message)
                        .action('OK')
                        .hideDelay(3000)
            );
        }

        User.fetch().then(function (user) {
            $scope.loggedUser = user;
        });

        var loadUsers = function () {
            $scope.promise = User.all($scope.query).then(function (data) {
                $scope.users       = data.users;
                $scope.query.total = data.total;
            });
        }

        loadUsers();

        $scope.onSearch = function () {
            $scope.query.page = 1;
            loadUsers();
        }

        $scope.onPaginationChange = function (page, limit) {
            $scope.query.page  = page;
            $scope.query.limit = limit;
            loadUsers();
        }

        $scope.openMenu = function ($mdOpenMenu, ev) {
            $mdOpenMenu(ev);
        }

        $scope.onSaveUser = function (ev) {

            $mdDialog.show({
                controller         : 'DialogUserController',
                templateUrl        : 'app/admin/views/modal/admin.user.modal.html',
                parent             : angular.element(document.body),
                targetEvent        : ev,
                locals             : {
                    user: null
                },
                clickOutsideToClose: true
            })
                     .then(function (answer) {
                         loadUsers();
                     });
        }

        $scope.onUpdateUser = function (ev, user) {

            var objUser = angular.copy(user);

            $mdDialog.show({
                controller         : 'DialogUserController',
                templateUrl        : 'app/admin/views/modal/admin.user.modal.html',
                parent             : angular.element(document.body),
                targetEvent        : ev,
                locals             : {
                    user: objUser
                },
                clickOutsideToClose: true
            })
                     .then(function (answer) {
                         loadUsers();
                     });
        };

        $scope.onDeleteUser = function (ev, user) {

            var confirm = $mdDialog.confirm()
                                   .title('Are you sure you want to delete the user?')
                                   .ok('Delete')
                                   .cancel('Cancel')
                                   .targetEvent(ev);

            $mdDialog.show(confirm).then(function () {

                User.delete({id: user.id}).then(function () {
                        loadUsers();
                        showToast('User ' + user.getUsername() + ' deleted');
                    },
                    function (error) {
                        console.log(error);
                        showToast(error.message);
                    });
            });
        };

    }

    function DialogUserController(User, ParseFile, $scope, $mdDialog, $mdToast, user) {

        $scope.imageFilename = '';
        $scope.objUser       = {
            roleName: 'User'
        };

        if (user) {
            $scope.objUser = user;
            if ($scope.objUser.photo) {
                $scope.imageFilename = $scope.objUser.photo.name();
            }
        }

        var showSimpleToast = function (message) {
            $mdToast.show(
                $mdToast.simple()
                        .content(message)
                        .action('OK')
                        .hideDelay(3000)
            );
        };

        $scope.uploadImage = function (file) {

            if (file === null) {
                return;
            } else if (file.type.match(/image.*/) === null) {
                showSimpleToast('File not supported');
                return;
            } else if (file.$error) {
                showSimpleToast('File too large. Max 2MB');
            } else {

                $scope.imageFilename = file.name;
                $scope.isUploading   = true;

                ParseFile.upload(file).then(function (savedFile) {
                    $scope.objUser.photo = savedFile;
                    $scope.isUploading   = false;
                    showSimpleToast('File uploaded');
                }, function (error) {

                    showSimpleToast(error.message);
                    $scope.isUploading   = false;
                    $scope.objUser.photo = null;

                });
            }
        }

        $scope.onEventSaveUser = function (isValidForm) {

            if (isValidForm) {

                if (!$scope.objUser.password) {
                    showSimpleToast('Password required');
                } else if ($scope.objUser.password.length < 6) {
                    showSimpleToast('Password should be at least 6 characters');
                } else {

                    $scope.isSavingUser = true;

                    User.create($scope.objUser).then(function (data) {
                        showSimpleToast('User saved');
                        $mdDialog.hide();
                        $scope.isSavingUser = false;
                    }, function (error) {
                        showSimpleToast(error.message);
                        $scope.isSavingUser = false;
                    });
                }
            } else {
                showSimpleToast('Please correct all highlighted errors and try again');
            }
        }

        $scope.onEventUpdateUser = function (isValidForm) {

            if (isValidForm) {

                if ($scope.objUser.password && $scope.objUser.password.length < 6) {
                    showSimpleToast('Password should be at least 6 characters');
                    return;
                }

                $scope.isSavingUser = true;

                User.update($scope.objUser).then(function (data) {
                    showSimpleToast('User updated');
                    $mdDialog.hide();
                    $scope.isSavingUser = false;
                }, function (error) {
                    showSimpleToast(error.message);
                    $scope.isSavingUser = false;
                });
            } else {
                showSimpleToast('Please correct all highlighted errors and try again');
            }
        }

        $scope.hide = function () {
            $mdDialog.cancel();
        }

        $scope.cancel = function () {
            $mdDialog.cancel();
        }

    }

})();

(function () {
  'use strict';
  angular
    .module('admin')
    .controller('AminGalleryActivityModalController', AminGalleryActivityModalController );

    function AminGalleryActivityModalController($scope, $mdDialog, $mdToast, Gallery, ParseFile, NgMap, GeoCoder, item) {

        var marker, map;

        $scope.gallery = {
            title: ''
        };
        $scope.input   = {};

        $scope.isCreating            = true;
        $scope.isImageOneUploading   = false;
        $scope.isImageTwoUploading   = false;
        $scope.isImageThreeUploading = false;
        $scope.isImageFourUploading  = false;

        if (item) {

            $scope.gallery = item;
            if ($scope.gallery.image) {
                $scope.imageOneFilename = $scope.gallery.image.name();
            }

            $scope.input.latitude  = item.location.latitude;
            $scope.input.longitude = item.location.longitude;

            $scope.isCreating = false;
        }

        var showSimpleToast = function (message) {
            $mdToast.show(
                $mdToast.simple()
                        .content(message)
                        .action('OK')
                        .hideDelay(3000)
            );
        };

        $scope.onAddressChanged = function () {
            GeoCoder.geocode({address: $scope.gallery.address}).then(function (result) {

                if (map) {

                    var location = result[0].geometry.location;
                    location     = new google.maps.LatLng(location.lat(), location.lng());

                    map.setCenter(location);
                    map.setZoom(15);

                    marker.setPosition(location);

                    $scope.gallery.location = new Parse.GeoPoint({
                        latitude : location.lat(),
                        longitude: location.lng()
                    });

                    $scope.input.latitude  = location.lat();
                    $scope.input.longitude = location.lng();
                }
            });
        }

        NgMap.getMap().then(function (objMap) {

            map    = objMap;
            marker = map.markers[0];

            // Fix gray area in second render
            google.maps.event.trigger(map, 'resize');

            if (item) {

                var galleryLocation = new google.maps.LatLng(
                    item.location.latitude,
                    item.location.longitude);

                map.setCenter(galleryLocation)
                marker.setPosition(galleryLocation);
                map.setZoom(15);
            } else {
                map.setZoom(1);
                map.setCenter(new google.maps.LatLng(0, 0));
            }
        });


        $scope.onMarkerDragEnd = function (ev) {

            var lat = ev.latLng.lat();
            var lng = ev.latLng.lng();

            $scope.gallery.location = new Parse.GeoPoint({
                latitude : lat,
                longitude: lng
            });

            $scope.input.latitude  = lat;
            $scope.input.longitude = lng;
        };

        $scope.onInputLocationChanged = function () {

            if ($scope.input.latitude && $scope.input.longitude && map) {

                $scope.gallery.location = new Parse.GeoPoint({
                    latitude : $scope.input.latitude,
                    longitude: $scope.input.longitude
                });

                marker.setPosition(new google.maps.LatLng(
                    $scope.input.latitude,
                    $scope.input.longitude
                ));

                map.setCenter(new google.maps.LatLng(
                    $scope.input.latitude,
                    $scope.input.longitude
                ));

                map.setZoom(12);
            }
        }

        $scope.uploadImageOne = function (file, invalidFile) {

            if (file) {

                $scope.isImageOneUploading = true;
                $scope.imageOneFilename    = file.name;

                ParseFile.upload(file).then(function (savedFile) {

                        $scope.gallery.image       = savedFile;
                        $scope.isImageOneUploading = false;
                        showSimpleToast('Image uploaded');
                    },
                    function (error) {
                        $scope.isImageOneUploading = false;
                        showSimpleToast(error.message);
                    });

            } else {
                if (invalidFile) {
                    if (invalidFile.$error === 'maxSize') {
                        showSimpleToast('Image too big. Max ' + invalidFile.$errorParam);
                    }
                }
            }
        };


        $scope.hide = function () {
            $mdDialog.cancel();
        };

        $scope.cancel = function () {
            $mdDialog.cancel();
        };

        $scope.onSaveGallery = function (isFormValid) {

            if (!isFormValid) {
                showSimpleToast('Please correct all highlighted errors and try again');
            } else if (!$scope.gallery.image) {
                showSimpleToast('Upload at least the first image');
            } else if (!$scope.gallery.location) {
                showSimpleToast('Ubication is required')
            }
            else {

                $scope.isSavingGallery = true;

                Gallery.create($scope.gallery).then(function (gallery) {
                        showSimpleToast('Gallery saved');
                        $mdDialog.hide();
                        $scope.isSavingGallery = false;
                    },
                    function (error) {
                        showSimpleToast(error.message);
                        $scope.isSavingGallery = false;
                    });
            }
        };

        $scope.onUpdateGallery = function (isFormValid) {

            if (!isFormValid) {
                showSimpleToast('Please correct all highlighted errors and try again');
            } else {

                $scope.isSavingGallery = true;

                Gallery.update($scope.gallery).then(function (gallery) {
                        showSimpleToast('Gallery updated');
                        $mdDialog.hide();
                        $scope.isSavingGallery = false;
                    },
                    function (error) {
                        showSimpleToast(error.message);
                        $scope.isSavingGallery = false;
                    });

            }
        };

    }

})();

