(function () {
    'use strict';
    angular.module('public').controller('UserRegisterCtrl', UserRegisterController);

    function UserRegisterController(User, $rootScope, Auth, $state, Toast) {
        var vm  = this;
        vm.form = {
            name                : '',
            email               : '',
            username            : '',
            password            : '',
            passwordConfirmation: ''
        }

        vm.genders = [
            {
                text : 'USER.FORM.MAN',
                value: 'man'
            },
            {
                text : 'USER.FORM.WOMAN',
                value: 'woman'
            }
        ];

        vm.validateUsername = function (input) {
            return User.validateUsername({input: input});
        };

        vm.validateEmail = function (input) {
            return User.validateEmail({input: input});
        };


        vm.onSubmit = function (rForm, form) {
            console.log(rForm, form);

            if (!rForm.$invalid) {
                vm.loading   = true;
                var userForm = angular.copy(form);
                delete userForm.passwordConfirmation;

                User.signUp(userForm).then(function (data) {
                    console.log(data);
                    Toast.simple("Server Installed");
                    User.signIn({
                        username: userForm.username,
                        password: userForm.password
                    }).then(function (user) {
                        console.log(user);
                        console.log(user.attributes);
                        vm.loading             = false;
                        $rootScope.currentUser = Auth.getLoggedUser();
                        console.log(Parse.User.current().attributes);
                        $state.go('app.feed');
                    }).catch(function (resp) {
                        console.log(resp);
                        Toast.simple('Invalid login');
                        vm.loading = false;
                    });

                }).catch(function (data) {
                    Toast.simple(data.message);
                    vm.loading = false;
                });
            } else {
                Toast.simple('Complete all fields');
            }
        }
        ;
    }


})();

(function () {
    'use strict';
    angular.module('admin').controller('UserResetPasswordCtrl', UserResetPasswordCtrl);

    function UserResetPasswordCtrl($scope, Toast, $mdDialog, Auth) {

        $scope.isLoading = false;

        var showDialog = function (title, message, ev) {

            $mdDialog.show(
                $mdDialog.alert()
                         .parent(angular.element(document.body))
                         .clickOutsideToClose(true)
                         .title(title)
                         .content(message)
                         .ariaLabel('Alert Dialog')
                         .ok('Ok')
                         .targetEvent(ev)
            );
        }

        $scope.onReset = function (rForm, form, $event) {

            if (!rForm.$invalid) {

                $scope.isLoading = true;
                Auth.resetPassword($scope.email).then(function (success) {
                    $scope.isLoading = false;
                    showDialog('Success', 'Check your email to reset your password', $event);
                }, function (error) {
                    $scope.isLoading = false;
                    showDialog('Error', error.message, $event);
                })
            }
        }

    }

})();
(function () {
    'use strict';
    angular.module('public').controller('FeedCtrl', FeedController);

    function FeedController(Gallery, $scope) {
        var vm = this;
        console.log('Feed Controller');

        $scope.params      = {};
        $scope.params.page = 1;
        $scope.data        = [];

        $scope.onReload = function () {
            $scope.params.page = 0;
            $scope.data        = [];
            showLoading();
            loadFeed();
            $scope.loading = true;
            $scope.$broadcast('scroll.refreshComplete');
        };

        $scope.onReload();

        var isLoadingViewShown   = false;
        var isGalleriesViewShown = false;
        var isErrorViewShown     = false;
        var isEmptyViewShown     = false;

        var isMoreData = false;

        function showLoading() {
            isLoadingViewShown   = true;
            isGalleriesViewShown = false;
            isErrorViewShown     = false;
            isEmptyViewShown     = false;
        }

        function showGalleries() {
            isGalleriesViewShown = true;
            isLoadingViewShown   = false;
            isErrorViewShown     = false;
            isEmptyViewShown     = false;
        }

        function showErrorView() {
            isErrorViewShown     = true;
            isGalleriesViewShown = false;
            isLoadingViewShown   = false;
            isEmptyViewShown     = false;
        }

        function showEmptyView() {
            isEmptyViewShown     = true;
            isErrorViewShown     = false;
            isGalleriesViewShown = false;
            isLoadingViewShown   = false;
        }


        function ensureMoreData(length) {
            isMoreData = false;
            if (length > 0) {
                isMoreData = true;
            }
        }

        function setGalleries(data) {
            for (var i = 0; i < data.length; i++) {
                $scope.data.push(data[i]);
            }
        }

        function setCurrentPage(page) {
            $scope.params.page = page;
        }

        function loadFeed() {

            Gallery.feed($scope.params).then(function (data) {
                ensureMoreData(data.length);
                setCurrentPage($scope.params.page + 1);
                setGalleries(data);

                if ($scope.data.length === 0) {
                    showEmptyView();
                } else {
                    showGalleries();
                }

                $scope.$broadcast('scroll.infiniteScrollComplete');
                $scope.$broadcast('scroll.refreshComplete');
                $scope.loading = false;

            }).catch(function () {
                if ($scope.data.length === 0) {
                    showErrorView();
                }
                isMoreData     = false;
                $scope.loading = false;
                $scope.$broadcast('scroll.refreshComplete');
            });
        }

        $scope.onLoadMore = function () {
            loadFeed();
        };

        $scope.moreDataCanBeLoaded = function () {
            return isMoreData;
        };

        $scope.showLoadingView = function () {
            return isLoadingViewShown;
        };

        $scope.showGalleries = function () {
            return isGalleriesViewShown;
        };

        $scope.showErrorView = function () {
            return isErrorViewShown;
        };

        $scope.showEmptyView = function () {
            return isEmptyViewShown;
        };


    }

})();
